* {
  padding: 0;
  margin: 0;
  
}

body {
  background: #c3d466;

}

.header-container {
  background: black;
  color: white;
  padding: 10px;
}

.search-form-container {
  text-align: center;
  margin: 10px;

}

.search-form-container input[type=text] {
  width: 400px;
  height: 50px;
  font-size: 30px;
  text-align: center;
  border: none;
  border-radius: 20px;
}

.search-form-container input[type=submit]{
  width: 100px;
  height: 50px;
  margin-top: 10px;
}

.display-weather-container {
  text-align: center;
  padding: 10px;
  margin: 10px;
  font-size: 30px;
}

.loading {
  text-align: center;
}